<template>
    <div class="tuoguan">
        <div v-if="$route.name === '公号托管'">
            <Button class="create" type="success" to="/manage/gonghao/tuoguan/create">接管新公号</Button>
            <Button style="float:right;" type="warning" @click="reset">
                <span style="color: #000;">(慎用)</span>
                重置code
            </Button>
            <Table class="tuoguan-table" stripe :columns="tuoguanHeader" :data="tuoguanData"></Table>
        </div>
        <router-view />
    </div>
</template>

<script>
export default {
  data() {
    return {
      isCreate: true,
      code: null,
      name: "",
      tuoguanData: [],
      tuoguanHeader: [
        {
          key: "code",
          title: "code",
          align: "center",
          width: 80
        },
        {
          key: "name",
          title: "公号名称",
          align: "center"
        },
        {
          key: "appid",
          title: "appid",
          align: "center"
        },
        {
          key: "appsecret",
          title: "appsecret",
          align: "center"
        },
        {
          key: "token",
          title: "token",
          align: "center"
        },
        {
          key: "EncodingAESKey",
          title: "EncodingAESKey",
          align: "center"
        },
        {
          key: "url",
          title: "链接",
          align: "center",
          render: (h, params) => {
            let url = "http://test.oorggt.top/weichat/" + params.row.code;
            return h(
              "span",
              {
                props: {}
              },
              url
            );
          }
        },
        {
          title: "Action",
          align: "center",
          width: 200,
          render: (h, params) => {
            let that = this;
            return h("div", [
              h(
                "Button",
                {
                  style: {
                    marginRight: "10px"
                  },
                  props: {
                    type: "primary",
                    size: "small"
                  },
                  on: {
                    click() {
                      that.$router.push({
                        name: "修改公号",
                        params: params.row
                      });
                    }
                  }
                },
                "编辑"
              ),
              h(
                "Button",
                {
                  style: {
                    marginRight: "10px"
                  },
                  props: {
                    type: "warning",
                    size: "small"
                  },
                  on: {
                    click() {
                      that.$router.push({
                        name: "配置公号",
                        params: { code: params.row.code }
                      });
                    }
                  }
                },
                "配置"
              ),
              h(
                "Button",
                {
                  props: {
                    type: "error",
                    size: "small"
                  },
                  on: {
                    click() {
                      that.$axios.get('/conf/del', {params: { id: params.row._id }})
                      .then(res => {
                        that.tuoguanData.splice(params.index, 1);
                        that.$Message.info(res.data.success);
                      });
                    }
                  }
                },
                "删除"
              )
            ]);
          }
        }
      ]
    };
  },
  mounted() {
    this.showDataList();
    // sessionStorage.setItem("secName", this.$route.name);
  },
  methods: {
    create() {
      this.openPage();
    },
    openPage() {
      store.commit("changeShowTable", false);
    },
    closePage() {
      store.commit("changeShowTable", true);
    },
    showDataList() {
      this.$axios.get("/conf").then(res => {
        this.tuoguanData = res.data.data;
      });
    },
    showAgain() {
      this.showDataList();
    },
    reset() {
      this.$axios.get("/conf/reset").then(res => {
        this.$Message.info(res.data.success);
      });
    }
  }
};
</script>

<style scoped>
.create {
  margin-bottom: 30px;
  float: left;
}

.tuoguan-table {
  width: 100%;
  clear: both;
}
</style>
